/**
 * @file common.js
 */

export const getUrlParam = name => {
    let reg = new RegExp('(^|(&|/?))' + name + '=([^&]*)(&|$)', 'i');
    let r = window.location.search.substr(1).match(reg);
    if (r !== null) {
        return (r[3]);
    }
    return '';
};

/**
 * loadImgs
 * @description 
 * @param {Array} list url
 * @param {Number} maxTime (ms)
 * @returns {Promise} Promise
 */
export const loadImgs = (list, maxTime = 800) => {
    if (!Array.isArray(list)) {
        return Promise.resolve();
    }
    return new Promise(resolve => {
        let count = list.length;
        list.forEach(url => {
            let flag = false; // 
            let img = new Image();
            img.src = url;
            const resolver = () => {
                if (flag) {
                    return;
                }
                count--;
                flag = true;
                count <= 0 && resolve();
            };
            img.onload = resolver;
            img.onerror = resolver;
            setTimeout(resolver, maxTime);
        });
    });
};

/**
 * invokeScheme
 * @description scheme
 * @param {String} scheme scheme
 * @returns {undefined} 
 */
export const invokeScheme = scheme => {
    if (!scheme) {
        console.error('schema should not be empty');
        return;
    }
    let $node = document.createElement('iframe');
    $node.style.display = 'none';
    $node.src = scheme;
    let body = document.body || document.getElementsByTagName('body')[0];
    body.appendChild($node);

    setTimeout(function () {
        body.removeChild($node);
        $node = null;
    }, 0);
};

/**
 * debounce
 * @param {Function} func
 * @param {Number} wait
 * @param {Boolean} immediate
 */
export const debounce = (func, wait, immediate) => {
    let timeout;
    return function () {
        let context = this;
        let args = arguments;
        let later = function () {
            timeout = null;
            if (!immediate) {
                func.apply(context, args);
            }
        };
        let callNow = immediate && !timeout;
        clearTimeout(timeout);
        timeout = setTimeout(later, wait);
        if (callNow) {
            func.apply(context, args);
        }
    };
};

/**
 * @description 
 */
export const scrollToBottom = (ref, func) => {
    if (!ref) {
        return;
    }
    // 
    let scrollTop = ref.scrollTop;
    // 
    let scrollHeight = ref.scrollHeight;
    // 
    let clientHeight = ref.clientHeight;

    // 1px
    if (scrollTop + clientHeight >= scrollHeight - 1 && scrollTop + clientHeight <= scrollHeight + 1){
        if (Object.prototype.toString.call(func) === '[object Function]') {
            // 
            func();
        }
    }
};