import boxx from '@baidu/boxx';
import isBox from '@baidu/xbox/is-box';

export const getProcessInfo = () => {
    const callback = 'window.getOutboxShowDataCB';
    // 
    if (isBox && boxx && boxx.canIUse('ugc.getOutboxShowData')) {
        boxx.ugc.getOutboxShowData({
            callback: callback,
            source_from: 'gk_video_dibar',
            mode: 'h5',
            action: 'com.baidu.channel.feed.profileugcstatus',
            isShow: '1',
            success: () => {
                console.log('');
            },
            fail: res => {
                console.log('', res || '');
            },
        }, callback);
    }
};

/**
* @description -
* @param callback string event.emit
* @param outNaTipFn function 
*/
export const datachannelRegister = () => {
    const callback = window.getChannel;
    // 
    if (isBox && boxx && boxx.canIUse('event.on')) {
        boxx.event.on({
            action: 'com.baidu.channel.feed.profileugcstatus',
            jscallback: callback,
            success: res => {
                console.log('-');
            },
            fail: res => {
                console.log('-', res || '-');
            },
        });
    }
};

/**
* @description -
* @param outNaTipFn function 
*/
export const datachannelUnRegister = () => {
    // 
    if (isBox && boxx && boxx.canIUse('event.off')) {
        boxx.event.off({
            action: 'com.baidu.channel.feed.profileugcstatus',
            success: res => {
                console.log('-');
            },
            fail: res => {
                console.log('-', res || '-');
            },
        });
    }
};

// 
export const watchPageState = (showCallback, hideCallback) => {
    // 
    let hidden;
    let visibilityChange;
    if (typeof document.hidden !== 'undefined') {
        // Opera 12.10 and Firefox 18 and later support
        hidden = 'hidden';
        visibilityChange = 'visibilitychange';
    } else if (typeof document.msHidden !== 'undefined') {
        hidden = 'msHidden';
        visibilityChange = 'msvisibilitychange';
    } else if (typeof document.webkitHidden !== 'undefined') {
        hidden = 'webkitHidden';
        visibilityChange = 'webkitvisibilitychange';
    }
    document.addEventListener(
        visibilityChange,
        () => {
            if (document[hidden]) {
                hideCallback?.();
            } else {
                showCallback?.();
            }
        },
        false
    );
};